*,
*::before,
*::after {
    margin: 0;
    box-sizing: border-box;
}

html {
    height: 100%;
}

body {
    min-height: 100vh;
    margin: 0;
}

main {
    min-height: 100vh;
    padding: 65px 40px 16px;
    background: #373AF5 url("img/bg.svg") no-repeat;
    background-size: cover;
    display: flex;
    flex-direction: column;
}

.title {
    max-width: 775px;
    margin: 0 auto;
    padding-bottom: 60px;
    font-family: Ubuntu, sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 102px;
    line-height: 0.95;
    text-align: left;
    text-align-last: right;
    color: #fff;
}

.forecast-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    margin: 0 4.5vw;
}

.main__p {
    margin-top: auto;
    text-transform: uppercase;
    text-align: center;
    font-family: Ubuntu, sans-serif;
    font-size: 14px;
    line-height: 18px;
    color: #fff;
    opacity: 0.6;
}

@media only screen and (max-width: 540px) {
    main {
        padding: 30px 12px 24px;
    }
    .title {
        max-width: 245px;
        font-size: 32px;
        padding-bottom: 24px;
    }
    .main__p {
        margin-top: 64px;
        font-size: 14px;
        line-height: 18px;
    }
}